*{
  margin:0;
  padding:0;
  font-family:Nunito;
}

.main{
  margin:auto;
  background: linear-gradient(90deg, #000000, #434343);
  height: 130vh;
  width:100%;
}

.title{
  padding:24px 0 20px 0;
  color:white;
}

.regioninput{
  background:none;
  border:none;
  border-bottom:1px solid white;
  color: white;
  padding: '120px';
  outline:none;
  font-size: 135%;
}

.regioninput::placeholder{
  font-family:Nunito;
  font-weight:450;
  
}

.user-weather{
  color: white;
  padding:30px;
  border-radius: 10px;
  border:1px solid white;
}

.weather-temp h1 , .weather-temp h4 , .weather-temp p{
  padding-bottom: 4px;
  
}

.weather-info p , .weather-info h2{
  text-align:center;
}

.buttonSearch{
  color: white;
  text-transform: uppercase;
}